.btn-rating {
     text-decoration: none;
    text-align: center;
    padding: 20px 54px;
    border: none;
    -webkit-border-radius: 22px;
    -moz-border-radius: 22px;
    border-radius: 22px;
    font: 33px Arial, Helvetica, sans-serif;
    font-weight: bold;
    color: #E5FFFF;
    background-color: #e41c35;
    -webkit-box-shadow: 0px 0px 2px #e41c35, inset 0px 0px 1px #ffffff;
    -moz-box-shadow: 0px 0px 2px #e41c35, inset 0px 0px 1px #ffffff;
    box-shadow: 0px 0px 2px #e41c35, inset 0px 0px 1px #ffffff;
}

