button:focus {
    outline: 0;
}

.text-buttons {
    font-size: 15px;
    font-weight: 400;
    letter-spacing: 1.5px;
}

.HeaderButton--android .Icon--24 {
    padding: 0 !important;
}

.coins__block {
    margin-top: -32px;
}

.buttons {
    margin-top: 15px;
}

.click__item {
    font-size: 400px;
    margin-top: 10%;
}

.click__item:active {
    opacity: 0.8;
}

.Search--android.Search--default .Search__before .Icon, .Search--android.Search--default .Search__after .Icon {
    padding: 0px;
    color: #aaaeb3;
    color: #aaaeb3;
    color: var(--icon_secondary);
}

